@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap");
:root {
  --yellow-color: #E4AD55;
  --text-color: #444;
  --active-nav: #dd9f3a;
  --light-yellow: #FFEBB6;
  --green-color: #65B674;
  --hover-green: #449b54;
}

.second-s {
  margin-top: 7em !important;
  line-height: 1.3em;
  margin: 1.5em;
}
.second-s h5 {
  font-size: 1.3rem;
  color: #444;
  line-height: 1.5;
  font-family: "Poppins Regular";
  font-weight: 400;
  text-align: center;
  margin-bottom: 2.5em;
}

.howTo {
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

.p-platform {
  line-height: 1.7em;
  width: 100%;
  font-size: 0.9rem;
  margin-bottom: 4em;
}

.src-card {
  margin-top: 1em;
  width: 90%;
  height: 150px;
  gap: 1em;
  justify-content: center;
  align-items: center;
}

.audio-video {
  display: none;
}

.pricing-photo {
  display: none;
}

.info-side h2 {
  color: var(--text-color);
  font-size: 1.05rem;
  margin-bottom: 0 !important;
}
.info-side p {
  margin-top: 0 !important;
  margin-top: 0.7em !important;
  color: var(--text-color);
  font-size: 0.8rem;
  width: 100%;
}

.arrow {
  display: none;
}

@media screen and (min-width: 760px) {
  .howTo {
    display: flex;
    justify-content: center !important;
    align-items: center !important;
  }
  .slider {
    width: 60%;
  }
  .src-card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100% !important;
  }
  .info-side h2 {
    font-size: 1.6rem;
    margin-bottom: 0.5em !important;
    font-family: "Inter";
    font-weight: 400;
    line-height: 1.3;
  }
  .info-side p {
    width: 100%;
    margin-bottom: 1.5em;
  }
  .left-side-platform {
    width: 40%;
    margin-bottom: auto;
    margin-top: 2em;
  }
  .heading-platform {
    width: 100%;
  }
}
@media screen and (min-width: 860px) {
  .src-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    height: 140px;
  }
  .pricing-photo {
    margin-right: auto !important;
    margin-left: auto !important;
    width: 285px;
    height: auto;
    display: unset;
  }
  .p-platform {
    width: 60%;
    margin-bottom: 0 !important;
    margin-bottom: 3em !important;
  }
  .second-s {
    margin-top: 22em !important;
  }
  .numbered h3 {
    margin-left: 0 !important;
    margin-top: 0 !important;
  }
}
@media screen and (min-width: 1060px) {
  .second-s h5 {
    font-size: 1.7rem;
  }
  .intro {
    text-align: center;
  }
  .src-card {
    max-width: 750px;
    margin-left: auto;
    margin-right: 10em;
  }
  .audio-video {
    width: 200px;
  }
  .left-side-platform {
    margin-top: 4em;
  }
  .heading-platform {
    font-size: 2.2rem;
    margin-bottom: 0 !important;
  }
  .p-platform {
    width: 40%;
    margin-left: auto;
    margin-right: auto;
  }
  .arrow {
    display: none;
  }
}
@media screen and (min-width: 1390px) {
  .howTo {
    margin-bottom: 15em !important;
    margin-left: auto;
    margin-right: auto;
    max-width: 1390px;
  }
}